.card {
    display: flex;
    flex-direction: column;
    width: 20rem;
    height: 30rem;
    overflow: hidden;
    background-color: black;
    color: white;
    border-radius: 10px;
    transition: all 0.5s;
    position: relative;
}

.card:hover {
    transform: scale(1.08);
}

.card img {
    width: 100%;
    height: 30%;
}

.cardDetail {
    padding: 10px;
}

.cardDetail h3 {
    color: red;
}

p {
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

.a {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: 1rem;
    padding: 1rem;
}

.link {
    background-color: orange;
    color: black;
    border-radius: 10px;
    padding: 1rem;
}

.link:hover {
    color: red;
}