.container {
    background-color: black;
    width: 100%;
    color: orange;
    display: flex;
    flex-direction: column;
    padding: 10px;
}

form {
    width: 70%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.top {
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
}

.search {
     width: 70%;
     padding: 10px;
     border-radius: 10px;
}

button {
    padding: 10px;
    background-color: orange;
    border-radius: 10px;
    border: none;
    font-weight: 700;
    cursor: pointer;
}

@media screen and (max-width: 575px) {
    form {
        flex-direction: column;
    }

    .search {
        width: 80%;
    }

    button {
        width: 80%;
    }

    .top {
        flex-direction: column;
    }
}